.about__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    width: 100%;
    max-width: 500px;
    font-weight: 300;
}

.about__container span:first-child {
    font-size: 35px;
    font-weight: 500;
}

.about__container span:nth-child(2),
.about__container span:nth-child(3) {
    display: block;
    margin-top: 20px;
    font-size: 20px;
}

.about__container span:last-child {
    display: block;
    margin-top: 20px;
    font-size: 20px;
    font-weight: 500;
}

.about__container span:last-child a {
    color: deepskyblue;
}